@import './common.scss';

@import './footer.scss';


header.fixed {
    position: fixed;
    height: 120px;
    width: 100%;
    top: 0;
    left: 0;
    right: 0;
     background: $white;
}
.burger {
    display: none;
    
}

.pets-header {
    
    
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 30px 0;
   
    
    .logo{
        text-decoration: none;
        .logo_title {
            font-family: 'Georgia';
            font-style: normal;
            font-weight: normal;
            font-size: 32px;
            line-height: 110%;
            letter-spacing: 0.06em;
            color: $main_grey;
            margin-bottom: 10px;
        }
        
        .logo_subtitle {
            font-family: 'Arial';
            font-size: 13px;
            line-height: 15px;
            letter-spacing: 0.1em;
            color: $main_grey;
            text-align: center;
        }
        
    }
    
    
    
   
  
    
    
}
.nav {
    display: flex;
    padding-bottom: 2px;
    list-style: none;

    
    .nav_item {
        margin-left: 35px;
        margin-right: 1px;
        
    }
    
    .nav_link {
        font-family: 'Arial';
        font-size: 15px;
        line-height: 160%;
        color: $main_grey;
        text-decoration: none;
        cursor: pointer;

        display: block;
        
        &:hover {
            color: $dark_grey_button;
        }
        
        &.nav_link--active {
            color: $dark_grey_button;

            border-bottom: 3px solid $primary;
        }
    }
    
}

.nav-mobile-wrapper {
    position: fixed;
    top: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    transition: right 0.3s ease-in-out;
    right: -100%;
    
    
    .nav-mobile {
        position: absolute;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        right: 0;
        padding: 0 38px;
        width: 320px;
    height: 100%;
    background: #292929;
    box-sizing: border-box;
        
        .nav_item-mobile {
            font-size: 32px;
            line-height: 160%;
            margin-bottom: 40px;
            text-align: center;
            
            
            .nav_link{
                color: #CDCDCD;
                text-decoration: none;
                
                &.nav_link--active {
                    color: #FAFAFA;
                    border-bottom: 3px solid #F1CDB3;
                }
                &.nav_link--inactive:hover {
                    color: #CDCDCD;
                    cursor: default;
                }
                &:hover{
                    color: #FAFAFA;
                }
            }
            
        }
        
    }
    
    
    
    &.--active {
        right: 0;
    }
    
    
}

.nav-mobile-wrapper::before {
    content: "";
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.7);
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
    visibility: hidden;
    opacity: 0;
}

    .nav-mobile-wrapper.--active::before {
    opacity: 1;
    visibility: visible;
}

body.--blocked {
    overflow: hidden;
    background: rgba(41, 41, 41, 0.6);
}

.pets {
    padding-top: 120px;
    position: relative;
    background: $light_grey_bg;
    box-sizing: border-box;
    
    .pets_header {
        font-size: 35px;
        font-weight: normal;
        line-height: 130%;
        text-align: center;
        letter-spacing: 0.06em;
        color: $main_grey;
        margin-bottom: 44px;
    }
    
    .pets_wrapper {
        padding: 80px 0 100px;
    }
    
    .pets_cards {
        margin-bottom: 45px;
        padding-right: 1px;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        
        
        .pet_card {
            background: $non_active_card_bg;
            width: 270px;
            height: 435px;
            padding-bottom: 30px;
            margin-right: 40px;
            box-sizing: border-box;
            border-radius: 9px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            margin: 15px 0;
            
            &:hover, &.--active {
                box-shadow: 0px 2px 35px 14px rgba(13, 13, 13, 0.04);
                background: $white;
                cursor: pointer;
            }
            &:hover .pet_card_btn {
                background: $primary_hover;
            }
           
            &:last-child, &:nth-child(4n) {
                margin-right: 0;
            }
            
            
            .pet_photo {
                width: 270px;
                height: 270px;
                margin-bottom: 29px;
            }
            .pet_name {
                font-family: 'Georgia';
                font-size: 20px;
                line-height: 23px;
                letter-spacing: 0.06em;
                color: $main_grey;
                margin-bottom: 30px;
                text-align: center;
            }
            .pet_card_btn {
                width: 187px;
                height: 52px;
                background: transparent;
                margin: 0 auto;
                
               
            }
        }
    }
    
    .pets_pagination {
        display: flex;
        justify-content: center;
        .pagination_item {
            
            width: 52px;
            height: 52px;
            background: transparent;
            margin: 0 11px 0 9px;
            font-weight: normal;
            font-size: 20px;
            line-height: 115%;
            letter-spacing: 0.06em;
            color: $dark_grey_button;
            padding-bottom: 3px;
            padding-left: 1px;
            &:hover {
                background: #FDDCC4;
                border-color: #FDDCC4;
            }
            
            &.--active {
                background: $primary;
                cursor: default;
                &:hover {
                    background: $primary;
                    border-color: $primary;
                }
            }
            &.--inactive {
                border: 2px solid #CDCDCD;
                color: #CDCDCD;
                cursor: default;
                &:hover {
                    background: transparent;
                }
            }
        }
    }
    
}

@media (max-width: 1279px ){
    .center {
        padding: 0 30px;
    }
    .pets .pets_wrapper {
        padding-bottom: 75px;
    }
    .pets .pets_cards {
        justify-content: center;
        margin-bottom: 10px;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: repeat(3, 1fr);
        max-width: 580px;
        margin: 0 auto 10px;
    }
    .pets .pets_cards .pet_card {
        margin: 0 20px 30px;
    }
    .pets .pets_header {
        margin-bottom: 30px;
    }
    
    .pets .pets_cards .pet_card:nth-child(7),  .pets .pets_cards .pet_card:nth-child(8) {
        display: none;
    }
    
    .pets .pets_cards .pet_card:nth-child(4n) {
        margin-right: 20px;
    }

}
@media (max-width: 767px) {
    .pets-header .nav {
        display: none;
    }
    .burger {
        display: flex;
        align-items: center;
        margin-right: 43px;
        z-index: 1;
        transition: transform 0.3s ease-in-out;
        
        &.--active {
            transform: rotate(90deg);
            
        }
    }
    .burger:hover {
        cursor: pointer;
    }
    .center {
        padding: 0 10px;
    }
    
    
    .pets .pets_wrapper {
        padding: 42px 0;
    }
    
    .pets .pets_cards {
        grid-template-columns: repeat(1, 1fr);
        max-width: 300px;
    }
    .pets .pets_cards .pet_card:nth-child(4), .pets .pets_cards .pet_card:nth-child(5), .pets .pets_cards .pet_card:nth-child(6){
        display: none; 
        
    
    }
        
        
    .pets .pets_header {
            font-size: 25px;
            line-height: 130%;
            letter-spacing: 0.06em;
            margin-bottom: 42px;
    }
    .pets .pets_pagination .pagination_item {
            margin: 0 5px;
    }
    .pets .pets_pagination .pagination_item:first-of-type{
            margin-left: 0;
    }
    .pets .pets_pagination .pagination_item:last-child{
            margin-right: 0;
    }
}
