footer {
  background: url("../images/noise_transparent2x.png"), radial-gradient(110.67% 538.64% at 5.73% 50%, #513D2F 0%, #1A1A1C 100%), #211F20;
  padding-top: 37px;
  height: 350px;
  box-sizing: border-box;
  overflow: hidden;
}
footer .footer_wrapper {
  display: flex;
  justify-content: space-between;
}
footer .footer_contacts {
  max-width: 300px;
}
footer .footer_location {
  max-width: 322px;
}
footer .footer_questions, footer .footer_visit {
  display: flex;
  flex-direction: column;
  font-weight: normal;
  font-size: 35px;
  line-height: 130%;
  letter-spacing: 0.06em;
  color: #ffffff;
  margin-top: 18px;
}
footer .footer_email {
  margin: 38px 0 40px;
}
footer .footer_address:first-of-type {
  margin: 32px 0 35px;
}
footer .footer_email,
footer .footer_phone,
footer .footer_address {
  font-weight: normal;
  font-size: 20px;
  line-height: 110%;
  letter-spacing: 0.06em;
  color: #F1CDB3;
  text-decoration: none;
  display: flex;
  align-items: center;
}
footer .footer_email img,
footer .footer_phone img,
footer .footer_address img {
  margin-right: 20px;
}
footer .footer_photo_puppy {
  width: 300px;
  height: 315px;
}

@media (max-width: 1279px) {
  footer {
    height: auto;
    padding-top: 30px;
  }

  footer .footer_wrapper {
    flex-wrap: wrap;
    justify-content: space-around;
  }

  footer .footer_contacts, footer .footer_location {
    margin-right: 0;
    max-width: 45%;
    box-sizing: border-box;
  }

  footer .footer_contacts {
    padding-left: 16px;
  }

  footer .footer_questions, footer .footer_visit {
    margin-top: 0;
  }

  footer .footer_photo_puppy {
    margin: 65px auto 0;
  }
}
@media (max-width: 767px) {
  footer .footer_wrapper {
    flex-direction: column;
    align-items: center;
  }

  footer .footer_questions, footer .footer_visit {
    text-align: center;
    font-size: 25px;
    line-height: 130%;
    letter-spacing: 0.06em;
  }

  footer .footer_contacts,
footer .footer_location {
    max-width: 100%;
    padding: 0;
  }

  footer .footer_photo_puppy {
    margin-top: 0;
    width: 260px;
    height: 269px;
  }

  footer .footer_photo_puppy img {
    width: 260px;
    height: 269px;
  }

  footer .footer_email, footer .footer_phone {
    margin: 40px auto;
    justify-content: center;
  }

  footer .footer_visit {
    max-width: 95%;
    margin: 0 auto;
  }

  footer .footer_address, footer .footer_address:first-of-type {
    margin: 40px auto;
    width: 300px;
  }

  footer .footer_contacts {
    margin-right: 0;
  }
}
header.fixed {
  position: fixed;
  height: 120px;
  width: 100%;
  top: 0;
  left: 0;
  right: 0;
  background: #ffffff;
}

.burger {
  display: none;
}

.pets-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 30px 0;
}
.pets-header .logo {
  text-decoration: none;
}
.pets-header .logo .logo_title {
  font-family: "Georgia";
  font-style: normal;
  font-weight: normal;
  font-size: 32px;
  line-height: 110%;
  letter-spacing: 0.06em;
  color: #545454;
  margin-bottom: 10px;
}
.pets-header .logo .logo_subtitle {
  font-family: "Arial";
  font-size: 13px;
  line-height: 15px;
  letter-spacing: 0.1em;
  color: #545454;
  text-align: center;
}

.nav {
  display: flex;
  padding-bottom: 2px;
  list-style: none;
}
.nav .nav_item {
  margin-left: 35px;
  margin-right: 1px;
}
.nav .nav_link {
  font-family: "Arial";
  font-size: 15px;
  line-height: 160%;
  color: #545454;
  text-decoration: none;
  cursor: pointer;
  display: block;
}
.nav .nav_link:hover {
  color: #292929;
}
.nav .nav_link.nav_link--active {
  color: #292929;
  border-bottom: 3px solid #F1CDB3;
}

.nav-mobile-wrapper {
  position: fixed;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  transition: right 0.3s ease-in-out;
  right: -100%;
}
.nav-mobile-wrapper .nav-mobile {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  right: 0;
  padding: 0 38px;
  width: 320px;
  height: 100%;
  background: #292929;
  box-sizing: border-box;
}
.nav-mobile-wrapper .nav-mobile .nav_item-mobile {
  font-size: 32px;
  line-height: 160%;
  margin-bottom: 40px;
  text-align: center;
}
.nav-mobile-wrapper .nav-mobile .nav_item-mobile .nav_link {
  color: #CDCDCD;
  text-decoration: none;
}
.nav-mobile-wrapper .nav-mobile .nav_item-mobile .nav_link.nav_link--active {
  color: #FAFAFA;
  border-bottom: 3px solid #F1CDB3;
}
.nav-mobile-wrapper .nav-mobile .nav_item-mobile .nav_link.nav_link--inactive:hover {
  color: #CDCDCD;
  cursor: default;
}
.nav-mobile-wrapper .nav-mobile .nav_item-mobile .nav_link:hover {
  color: #FAFAFA;
}
.nav-mobile-wrapper.--active {
  right: 0;
}

.nav-mobile-wrapper::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.7);
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  visibility: hidden;
  opacity: 0;
}

.nav-mobile-wrapper.--active::before {
  opacity: 1;
  visibility: visible;
}

body.--blocked {
  overflow: hidden;
  background: rgba(41, 41, 41, 0.6);
}

.pets {
  padding-top: 120px;
  position: relative;
  background: #F6F6F6;
  box-sizing: border-box;
}
.pets .pets_header {
  font-size: 35px;
  font-weight: normal;
  line-height: 130%;
  text-align: center;
  letter-spacing: 0.06em;
  color: #545454;
  margin-bottom: 44px;
}
.pets .pets_wrapper {
  padding: 80px 0 100px;
}
.pets .pets_cards {
  margin-bottom: 45px;
  padding-right: 1px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
.pets .pets_cards .pet_card {
  background: #FAFAFA;
  width: 270px;
  height: 435px;
  padding-bottom: 30px;
  margin-right: 40px;
  box-sizing: border-box;
  border-radius: 9px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 15px 0;
}
.pets .pets_cards .pet_card:hover, .pets .pets_cards .pet_card.--active {
  box-shadow: 0px 2px 35px 14px rgba(13, 13, 13, 0.04);
  background: #ffffff;
  cursor: pointer;
}
.pets .pets_cards .pet_card:hover .pet_card_btn {
  background: #FDDCC4;
}
.pets .pets_cards .pet_card:last-child, .pets .pets_cards .pet_card:nth-child(4n) {
  margin-right: 0;
}
.pets .pets_cards .pet_card .pet_photo {
  width: 270px;
  height: 270px;
  margin-bottom: 29px;
}
.pets .pets_cards .pet_card .pet_name {
  font-family: "Georgia";
  font-size: 20px;
  line-height: 23px;
  letter-spacing: 0.06em;
  color: #545454;
  margin-bottom: 30px;
  text-align: center;
}
.pets .pets_cards .pet_card .pet_card_btn {
  width: 187px;
  height: 52px;
  background: transparent;
  margin: 0 auto;
}
.pets .pets_pagination {
  display: flex;
  justify-content: center;
}
.pets .pets_pagination .pagination_item {
  width: 52px;
  height: 52px;
  background: transparent;
  margin: 0 11px 0 9px;
  font-weight: normal;
  font-size: 20px;
  line-height: 115%;
  letter-spacing: 0.06em;
  color: #292929;
  padding-bottom: 3px;
  padding-left: 1px;
}
.pets .pets_pagination .pagination_item:hover {
  background: #FDDCC4;
  border-color: #FDDCC4;
}
.pets .pets_pagination .pagination_item.--active {
  background: #F1CDB3;
  cursor: default;
}
.pets .pets_pagination .pagination_item.--active:hover {
  background: #F1CDB3;
  border-color: #F1CDB3;
}
.pets .pets_pagination .pagination_item.--inactive {
  border: 2px solid #CDCDCD;
  color: #CDCDCD;
  cursor: default;
}
.pets .pets_pagination .pagination_item.--inactive:hover {
  background: transparent;
}

@media (max-width: 1279px) {
  .center {
    padding: 0 30px;
  }

  .pets .pets_wrapper {
    padding-bottom: 75px;
  }

  .pets .pets_cards {
    justify-content: center;
    margin-bottom: 10px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, 1fr);
    max-width: 580px;
    margin: 0 auto 10px;
  }

  .pets .pets_cards .pet_card {
    margin: 0 20px 30px;
  }

  .pets .pets_header {
    margin-bottom: 30px;
  }

  .pets .pets_cards .pet_card:nth-child(7), .pets .pets_cards .pet_card:nth-child(8) {
    display: none;
  }

  .pets .pets_cards .pet_card:nth-child(4n) {
    margin-right: 20px;
  }
}
@media (max-width: 767px) {
  .pets-header .nav {
    display: none;
  }

  .burger {
    display: flex;
    align-items: center;
    margin-right: 43px;
    z-index: 1;
    transition: transform 0.3s ease-in-out;
  }
  .burger.--active {
    transform: rotate(90deg);
  }

  .burger:hover {
    cursor: pointer;
  }

  .center {
    padding: 0 10px;
  }

  .pets .pets_wrapper {
    padding: 42px 0;
  }

  .pets .pets_cards {
    grid-template-columns: repeat(1, 1fr);
    max-width: 300px;
  }

  .pets .pets_cards .pet_card:nth-child(4), .pets .pets_cards .pet_card:nth-child(5), .pets .pets_cards .pet_card:nth-child(6) {
    display: none;
  }

  .pets .pets_header {
    font-size: 25px;
    line-height: 130%;
    letter-spacing: 0.06em;
    margin-bottom: 42px;
  }

  .pets .pets_pagination .pagination_item {
    margin: 0 5px;
  }

  .pets .pets_pagination .pagination_item:first-of-type {
    margin-left: 0;
  }

  .pets .pets_pagination .pagination_item:last-child {
    margin-right: 0;
  }
}